<template>
  <div class="home">
    <header>
      <div class="grid">
        <Header @addHistoryItem="addHistoryItemParent" />
      </div>
    </header>
    <div class="grid main-wrapper">
      <section class="history-sidebar">
        <History :arrHistory="arrHistory" @addHistoryItem="addHistoryItemParent" />
      </section>
      <section class="main">
        <div v-if="statusData === 'done'">
          <div class="level level-1">
            <div class="item short-data">
              <ShortData
                :name="shortData.name"
                :nickName="shortData.nickName"
                :shortID="shortData.shortID"
                :telegramID="shortData.telegramID"
                :clientID="shortData.clientID"
              />
            </div>
          </div>
          <div class="level level-2">
            <div class="item common-data">
              <CommonData />
            </div>
            <div class="item insta-bonus">
              <InstaBonusStatus />
            </div>
          </div>

          <div class="level">
            <div class="item sprint-data">
              <SprintData />
            </div>
          </div>

          <div class="level">
            <div class="item prepaid-sprints">
              <PrepaidSprints />
            </div>
          </div>

          <div class="level level-4">
            <div class="item current-transactions">
              <q-expansion-item
                v-model="currentTransactions.is"
                :label="currentTransactions.label"
                @show="toggleItem(true, 'currentTransactions')"
                @hide="toggleItem(false, 'currentTransactions')"
              >
                <CurrentTransactions />
              </q-expansion-item>
            </div>
          </div>
          <div class="level level-5">
            <div class="item failed-transactions">
              <q-expansion-item
                v-model="failedTransactions.is"
                :label="failedTransactions.label"
                @show="toggleItem(true, 'failedTransactions')"
                @hide="toggleItem(false, 'failedTransactions')"
              >
                <FailedTransactions />
              </q-expansion-item>
            </div>
          </div>
          <div class="level level-6">
            <div class="item successful-transactions">
              <q-expansion-item
                v-model="successfulTransactions.is"
                :label="successfulTransactions.label"
                @show="toggleItem(true, 'successfulTransactions')"
                @hide="toggleItem(false, 'successfulTransactions')"
              >
                <SuccessfulTransactions />
              </q-expansion-item>
            </div>
          </div>
          <div class="level level-7">
            <div class="item list-relatives">
              <ListRelatives />
            </div>
          </div>
          <div class="level level-8">
            <div class="item tree">Tree</div>
            <div class="item recent-bot-events">
              <RecentBotEvents />
            </div>
          </div>
        </div>

        <div v-else-if="statusData === 'load'" class="load-data">
          <q-spinner-tail color="primary" size="2em" />
        </div>
        <div v-else></div>
      </section>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import History from "@/components/History.vue";
import InstaBonusStatus from "@/components/tables/InstaBonusStatus.vue";
import ShortData from "@/components/tables/ShortData.vue";
import SprintData from "@/components/tables/SprintData.vue";
import PrepaidSprints from "@/components/tables/PrepaidSprints.vue";
import CurrentTransactions from "@/components/tables/CurrentTransactions.vue";
import FailedTransactions from "@/components/tables/FailedTransactions.vue";
import SuccessfulTransactions from "@/components/tables/SuccessfulTransactions.vue";
import ListRelatives from "@/components/tables/ListRelatives.vue";
import RecentBotEvents from "@/components/tables/RecentBotEvents.vue";
import CommonData from "@/components/common/CommonData.vue";

export default {
  name: "Home",
  components: {
    Header,
    InstaBonusStatus,
    CommonData,
    ShortData,
    SprintData,
    PrepaidSprints,
    CurrentTransactions,
    SuccessfulTransactions,
    FailedTransactions,
    ListRelatives,
    RecentBotEvents,
    History
  },
  data: function() {
    return {
      statusData: "",
      shortData: {},
      oldLabel: {
        currentTransactions: "",
        failedTransactions: "",
        successfulTransactions: ""
      },
      currentTransactions: {
        is: false,
        label: "Текущие транзакции"
      },
      failedTransactions: {
        is: false,
        label: "Неудачные транзакции"
      },
      successfulTransactions: {
        is: false,
        label: "Удачные транзакции"
      },
      arrHistory: [
        /*  {
          search: "Коля kolya ht67ed 4333334343 787878",
          name: "Коля",
          nickName: "kolya",
          shortID: "ht67ed",
          telegramID: "4333334343",
          clientID: "787878"
        } */
      ]
    };
  },
  computed: {
    statusLoadUserData() {
      return this.$store.state.statusLoadUserData;
    }
  },
  watch: {
    statusLoadUserData(val) {
      this.statusData = val;
    }
  },
  methods: {
    toggleItem(bool, objStr) {
      if (bool) {
        this.oldLabel[objStr] = this[objStr].label;
        this[objStr].label = "";
      } else {
        setTimeout(() => {
          this[objStr].label = this.oldLabel[objStr];
        }, 200);
      }
    },
    addHistoryItemParent(value) {
      this.shortData = value;
      this.arrHistory.unshift(value);
    }
  },
  mounted() {},
  created() {}
};
</script>

