<template>
  <div class="header">
    <multiselect
      v-model="selected"
      :options="options"
      label="search"
      track-by="name"
      :loading="isLoading"
      placeholder
      @select="updateSelected"
      @search-change="asyncFind"
    >
      <template slot="singleLabel" slot-scope="props">
        <div class="line">
          <div class="item">{{ props.option.name }}</div>
          <div class="item">{{ props.option.nickName }}</div>
          <div class="item">{{ props.option.shortID }}</div>
          <div class="item">{{ props.option.telegramID }}</div>
          <div class="item">{{ props.option.clientID }}</div>
        </div>
      </template>
      <template slot="option" slot-scope="props">
        <div class="line">
          <div class="item">{{ props.option.name }}</div>
          <div class="item">{{ props.option.nickName }}</div>
          <div class="item">{{ props.option.shortID }}</div>
          <div class="item">{{ props.option.telegramID }}</div>
          <div class="item">{{ props.option.clientID }}</div>
        </div>
      </template>
    </multiselect>

