 <script>
import Multiselect from "vue-multiselect";
import "vue-multiselect/dist/vue-multiselect.min.css";
export default {
  name: "Header",
  components: {
    Multiselect
  },
  data: function() {
    return {
      selected: null,
      isLoading: false,
      options: []
    };
  },
  computed: {
    token() {
      return this.$store.state.token;
    },
    statusLoadUserData: {
      get() {
        return this.$store.state.statusLoadUserData;
      },
      set(value) {
        this.$store.commit("setStatusLoadUserData", value);
      }
    }
  },
  watch: {},
  methods: {
    updateSelected(value) {
      this.statusLoadUserData = "load";
      this.$emit("addHistoryItem", value);
      this.$store.dispatch("getUserData", value.clientID);
      this.options = [];
    },
    async asyncFind(query) {
      if (!query) {
        return;
      }
      this.isLoading = true;
      let response, result;

      try {
        response = await this.$http.post(
          `odb/admin/find_by_props?find=${query}`,
          {
            token: this.token
          }
        );

        result = await response.json();
      } catch (error) {
        console.log("odb/admin/find_by_props?find= ", error);
      }
      this.isLoading = false;
      this.createSearchArray(result);
    },
    createSearchArray(arrData) {
      let arrTemp = [];
      for (const data of arrData) {
        arrTemp.push({
          name: data.first_name,
          nickName: data.username,
          shortID: data.short_id,
          telegramID: data.uid,
          clientID: data.id,
          search: `${data.first_name} ${data.username} ${data.short_id} ${data.uid} ${data.id}`
        });
      }

      this.options = arrTemp;
    }
  },
  mounted() {},
  created() {}
};
</script>

