import Vue from 'vue';
import Vuex from 'vuex';
import router from '../router/index';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    token: localStorage.getItem('user-token') || '',
    status: '',
    statusLoadUserData: '',
    userData: {},
  },
  mutations: {
    setStatusLoadUserData(state, payload) {
			console.log(payload);
      state.statusLoadUserData = payload;
    },
    userRequest(state, payload) {
      state.status = 'loading';
    },
    userSuccess(state, resp) {
      state.status = 'success';
      state.userData = resp;
      state.statusLoadUserData = 'done';
    },
    userError(state, err) {
      state.status = 'error';
      if (err === 'invalid_admin_token' && state.token != '') {
        /* state.arrNotification.push({
          message: 'Сервер был перезагружен',
          status: 'warning'
        }); */
        router.push('/login');
      } else {
        // state.arrNotification.push({ message: err, status: 'danger' });
      }
      console.error('userDataError  ', err);
    },
    authRequest(state, payload) {
      state.status = 'loading';
    },
    authSuccess(state, resp) {
      state.status = 'success';
      state.token = resp.token;
      state.hasLoadedOnce = true;
    },
    authError(state, err) {
      state.status = 'error';
      state.hasLoadedOnce = true;
      if (err === 'invalid_admin_token' && state.token != '') {
        // state.arrNotification.push({
        //   message: 'Сервер был перезагружен',
        //   status: 'warning'
        // });
        router.push('/login');
      } else {
        // state.arrNotification.push({ message: err, status: 'danger' });
      }
      console.error('authError  ', err);
    },
    authLogout(state, payload) {
      state.token = '';
    },
  },
  actions: {
    async authorization({ commit, dispatch, state }, { login, password }) {
      return new Promise((resolve, reject) => {
        commit('authRequest');
        Vue.http
          .post('odb/admin/auth', {
            login,
            password,
          })
          .then((resp) => {
            return resp.json();
          })
          .then((result) => {
            localStorage.setItem('user-token', result.token);
            commit('authSuccess', result);
            resolve(result);
          })
          .catch((err) => {
            commit('authError', err);
            localStorage.removeItem('user-token');
            reject(err);
          });
      });
    },
    async getUserData({ state, commit, dispatch }, id) {
      commit('userRequest');

      console.log('id ', id);

      Vue.http
        .post(`odb/admin/get_info_support?id=${id}`, {
          token: state.token,
        })
        .then((resp) => {
          return resp.json();
        })
        .then((result) => {
          if (result.error) {
            commit('userError', result.error);
          }
          commit('userSuccess', result);
          console.log('wwwwwwwwwwwwwwwwwwwwwww ', state.userData);
        })
        .catch((err) => {
          commit('userError', err);
          commit('authLogout');
        });
    },
    logout({ commit }) {
      return new Promise((resolve) => {
        commit('authLogout');
        localStorage.removeItem('user-token');
        resolve();
      });
    },
  },
  getters: {
    isAuthenticated: (state) => !!state.token,
    authStatus: (state) => state.status,
  },
  modules: {},
});
