<template>
  <div class="history">
    <div
      class="item"
      @click="goToUser(history)"
      v-for="(history, index) of arrHistory"
      :key="index"
    >
      <p class="name">{{ history.name }}</p>
      <div class="inner">
        <p>👤 {{ history.nickName }}</p>
        <p>🎫 {{ history.shortID }}</p>
        <p>🎫 {{ history.telegramID }}</p>
        <p>🎫 {{ history.clientID }}</p>
      </div>
    </div>
  </div>
</template>

 <script>
export default {
  name: "History",
  props: {
    arrHistory: Array
  },
  data: function() {
    return {};
  },
  computed: {},
  watch: {},
  methods: {
    goToUser(history) {
      this.$emit("addHistoryItem", history);
      this.$store.dispatch("getUserData", history.clientID);
    }
  },
  mounted() {},
  created() {}
};
</script>

 <style scoped>
.history {
  position: sticky;
  top: 10rem;
  height: calc(100vh - 13rem);
  display: flex;
  flex-direction: column;
  overflow: auto;
}

.history .item {
  margin-bottom: 2rem;
  padding: 0.5rem;
  border: 1px solid #000;
  border-radius: 0.5rem;
  cursor: pointer;
  transition: all 0.075s ease-in-out;
}

.history .item:hover {
  border: 1px solid hsl(var(--accent-color-normal));
}

.history .item p {
  margin: 0;
  font-size: 80%;
}

.history .item .name {
  font-size: 90%;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.history .item .inner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 0.5rem;
}
</style>