<template>
  <div class="login">
    <h3 class="auth-error" v-if="error">Ошибка авторизации</h3>
    <div class="wrapper">
      <div class="grid">
        <div class="wrapp-input">
          <q-input v-model="userLogin" label="Логин" />
          <q-input v-model="userPassword" label="Пароль" />
        </div>

        <q-btn @click="login" color="primary" class="full-width" label="Войти" />
        <q-btn @click="logout" color="negative" class="full-width" label="Выйти" />
      </div>
    </div>
  </div>
</template>

 <script>
export default {
  name: "Login",
  data: function() {
    return {
      userLogin: "",
      userPassword: "",
      error: false
    };
  },
  computed: {},
  watch: {},
  methods: {
    async login() {
      try {
        await this.$store.dispatch("authorization", {
          login: this.userLogin,
          password: this.userPassword
        });

        this.$router.push("/");
      } catch (err) {
        this.error = true;
        setTimeout(() => {
          this.error = false;
        }, 2000);
        console.error("Ошибка авторизации " + err);
      }
    },
    logout() {
      this.$store.dispatch("logout").then(() => {
        this.$router.push("/login");
      });
    }
    /*  checkLogin() {
      let userAyth = JSON.parse(localStorage.getItem('userAyth'));
      if (userAyth && userAyth.login && userAyth.password) {
        this.login = userAyth.login;
        this.password = userAyth.password;
      }
    } */
  },
  mounted() {},
  created() {
    // this.checkLogin();
  }
};
</script>

 <style scoped>
.login {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.login .wrapper {
  display: flex;
  flex-direction: column;
  max-width: 500px;
  min-width: 250px;
  width: 100%;
}

.login .wrapper input,
.login .wrapper button {
  margin-bottom: 1rem;
}

.auth-error {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
}

.wrapp-input {
  margin-bottom: 1rem;
}
</style>